import { Avatar, Card, CardContent, Checkbox, Divider, FormControlLabel, Stack, Switch, Typography } from '@mui/material';
import React, { useContext, useEffect, useState } from 'react';
import { AppContext } from 'components/common/context/AppProvider';
import { cleanUnderscore, getCoinsArray, notateNumber, prefix } from '@utility/helpers';
import styled from '@emotion/styled';
import CoinDisplay from 'components/common/CoinDisplay';
import Tooltip from 'components/Tooltip'; // Grid version 2
import { NextSeo } from 'next-seo';
import { isRiftBonusUnlocked } from '@parsers/world-4/rift';
import { CardTitleAndValue } from '@components/common/styles';
import { calcStampLevels, getStampBonus, unobtainableStamps, updateStamps } from '@parsers/stamps';
import Grid from '@mui/material/Unstable_Grid2';
import { grey } from '@mui/material/colors';
import ArrowRightAltIcon from '@mui/icons-material/ArrowRightAlt';
import Link from '@mui/material/Link';
import { useRouter } from 'next/router';
const Stamps = () => {
  const router = useRouter();
  const {
    state
  } = useContext(AppContext);
  const gildedStamps = isRiftBonusUnlocked(state?.account?.rift, 'Stamp_Mastery') ? state?.account?.accountOptions?.[154] : 0;
  const [forcedGildedStamp, setForcedGildedStamp] = useState(gildedStamps > 0);
  const stampReducer = state?.account?.atoms?.stampReducer;
  const [subtractGreenStacks, setSubtractGreenStacks] = React.useState(false);
  const [localStamps, setLocalStamps] = useState(state?.account?.stamps);
  useEffect(() => {
    setLocalStamps(updateStamps(state?.account, state?.characters, forcedGildedStamp));
  }, [forcedGildedStamp, state]);
  const getBorder = ({
    materials,
    level,
    hasMoney,
    hasMaterials,
    greenStackHasMaterials,
    enoughPlayerStorage
  }) => {
    if (level <= 0) return '';
    if (!hasMoney) {
      return 'warning.light';
    } else if (!hasMaterials || subtractGreenStacks && !greenStackHasMaterials) {
      return 'error.light';
    } else if (!enoughPlayerStorage) {
      return '#e3e310';
    } else if (materials.length > 0) {
      return '';
    }
    return materials.length === 0 && hasMaterials && hasMoney && enoughPlayerStorage ? 'info.light' : '';
  };
  return <div data-sentry-component="Stamps" data-sentry-source-file="stamps.jsx">
      <NextSeo title="Stamps | Idleon Toolbox" description="Keep track of your stamps levels and requirements" data-sentry-element="NextSeo" data-sentry-source-file="stamps.jsx" />
      <Stack direction={'row'} justifyContent={'center'} gap={1} data-sentry-element="Stack" data-sentry-source-file="stamps.jsx">
        <Tooltip title={'Missing Money'} data-sentry-element="Tooltip" data-sentry-source-file="stamps.jsx">
          <Avatar sx={{
          bgcolor: 'warning.light',
          width: 24,
          height: 24
        }} alt={'m'} src={''} data-sentry-element="Avatar" data-sentry-source-file="stamps.jsx">&nbsp;</Avatar>
        </Tooltip>
        <Tooltip title={'Missing Materials'} data-sentry-element="Tooltip" data-sentry-source-file="stamps.jsx">
          <Avatar sx={{
          bgcolor: 'error.light',
          width: 24,
          height: 24
        }} alt={'m'} src={''} data-sentry-element="Avatar" data-sentry-source-file="stamps.jsx">&nbsp;</Avatar>
        </Tooltip>
        <Tooltip title={'Not Enough Player Storage'} data-sentry-element="Tooltip" data-sentry-source-file="stamps.jsx">
          <Avatar sx={{
          bgcolor: '#e3e310',
          width: 24,
          height: 24
        }} alt={'m'} src={''} data-sentry-element="Avatar" data-sentry-source-file="stamps.jsx">&nbsp;</Avatar>
        </Tooltip>
        <Tooltip title={'Equipments'} data-sentry-element="Tooltip" data-sentry-source-file="stamps.jsx">
          <Avatar sx={{
          bgcolor: 'grey',
          width: 24,
          height: 24
        }} alt={'m'} src={''} data-sentry-element="Avatar" data-sentry-source-file="stamps.jsx">&nbsp;</Avatar>
        </Tooltip>
        <Tooltip title={'Upgradeable'} data-sentry-element="Tooltip" data-sentry-source-file="stamps.jsx">
          <Avatar sx={{
          bgcolor: 'info.light',
          width: 24,
          height: 24
        }} alt={'m'} src={''} data-sentry-element="Avatar" data-sentry-source-file="stamps.jsx">&nbsp;</Avatar>
        </Tooltip>
      </Stack>
      <Typography textAlign={'center'} component={'div'} variant={'caption'} mt={1} data-sentry-element="Typography" data-sentry-source-file="stamps.jsx">* Blue border means you have enough
        material, money and space to
        craft</Typography>
      <Stack mt={1} direction={'row'} gap={3} justifyContent={'center'} flexWrap={'wrap'} data-sentry-element="Stack" data-sentry-source-file="stamps.jsx">
        <CardTitleAndValue title={'Gilded Stamp'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="stamps.jsx">
          <Stack alignItems={'center'} direction={'row'} gap={2} data-sentry-element="Stack" data-sentry-source-file="stamps.jsx">
            <img src={`${prefix}data/GildedStamp.png`} alt="" />
            <Stack data-sentry-element="Stack" data-sentry-source-file="stamps.jsx">
              <Typography data-sentry-element="Typography" data-sentry-source-file="stamps.jsx">Owned: {gildedStamps}</Typography>
              <Typography data-sentry-element="Typography" data-sentry-source-file="stamps.jsx">Chance: {calcStampLevels(state?.account?.stamps) / 100}%</Typography>
            </Stack>
          </Stack>
          <FormControlLabel control={<Switch checked={forcedGildedStamp} onChange={() => setForcedGildedStamp(!forcedGildedStamp)} />} label="Gilded Stamp" data-sentry-element="FormControlLabel" data-sentry-source-file="stamps.jsx" />
        </CardTitleAndValue>
        <CardTitleAndValue title={'Stamp Reducer'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="stamps.jsx">
          <Stack alignItems={'center'} direction={'row'} gap={2} data-sentry-element="Stack" data-sentry-source-file="stamps.jsx">
            <img src={`${prefix}data/Atom0.png`} height={36} alt="" />
            {stampReducer ?? 0}%
          </Stack>
        </CardTitleAndValue>
        <CardTitleAndValue title={'Options'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="stamps.jsx">
          <Stack data-sentry-element="Stack" data-sentry-source-file="stamps.jsx">
            <Link underline={'hover'} sx={{
            cursor: 'pointer'
          }} onClick={() => router.push({
            pathname: 'old-stamps'
          })} data-sentry-element="Link" data-sentry-source-file="stamps.jsx">
              <Stack direction={'row'} alignItems={'center'} gap={1} data-sentry-element="Stack" data-sentry-source-file="stamps.jsx">
                <ArrowRightAltIcon data-sentry-element="ArrowRightAltIcon" data-sentry-source-file="stamps.jsx" />
                <Typography data-sentry-element="Typography" data-sentry-source-file="stamps.jsx">Old Stamps Page</Typography>
              </Stack>
            </Link>
            <FormControlLabel control={<Checkbox name={'mini'} checked={subtractGreenStacks} onChange={() => setSubtractGreenStacks(!subtractGreenStacks)} size={'small'} />} label={'Subtract Green Stacks'} data-sentry-element="FormControlLabel" data-sentry-source-file="stamps.jsx" />
          </Stack>
        </CardTitleAndValue>
      </Stack>
      <Grid container sx={{
      justifyContent: 'center'
    }} spacing={2} data-sentry-element="Grid" data-sentry-source-file="stamps.jsx">
        {Object.entries(localStamps || {}).map(([category, stamps], categoryIndex) => {
        return <Grid xs={12} md={6} lg={4} container spacing={.5} sx={{
          alignContent: 'start',
          justifyContent: 'center'
        }} key={category + '' + categoryIndex}>
            <Typography sx={{
            flexBasis: '100%'
          }} variable={'subtitle2'}>{category.capitalize()}</Typography>
            {stamps.map((stamp, stampIndex) => {
            const {
              rawName,
              level,
              materials,
              hasMaterials,
              greenStackHasMaterials,
              hasMoney,
              enoughPlayerStorage,
              reqItemMultiplicationLevel,
              displayName,
              bestCharacter
            } = stamp;
            const bonus = getStampBonus(state?.account, category, rawName, bestCharacter);
            const border = getBorder(stamp);
            const isBlank = displayName === 'Blank';
            return <Grid xs={4} sm={3} key={rawName + stampIndex}>
                <Tooltip maxWidth={450} title={isBlank ? '' : <StampInfo {...stamp} bonus={bonus} subtractGreenStacks={subtractGreenStacks} />}>
                  <Card sx={{
                  cursor: 'pointer',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  minHeight: 50,
                  border: materials.length === 0 && (subtractGreenStacks ? greenStackHasMaterials : hasMaterials) && hasMoney && enoughPlayerStorage && (level + 1) % reqItemMultiplicationLevel !== 0 && level > 0 ? '1px solid' : '',
                  borderColor: border
                }} variant={'outlined'} onClick={() => window.open(`https://idleon.wiki/wiki/${displayName}`, '_blank')}>
                    <CardContent sx={{
                    '&:last-child': {
                      p: 0
                    }
                  }}>
                      <Stack direction={'row'} alignItems={'center'} justifyContent={'space-around'}>
                        <StampIcon width={40} height={40} level={level} src={`${prefix}data/${rawName}.png`} alt="" />
                        <Typography>{level}</Typography>
                      </Stack>
                    </CardContent>
                  </Card>
                </Tooltip>
              </Grid>;
          })}
          </Grid>;
      })}
      </Grid>
    </div>;
};
const StampInfo = ({
  displayName,
  level,
  effect,
  multiplier,
  goldCost,
  materialCost,
  futureCosts,
  bestCharacter,
  subtractGreenStacks,
  ownedMats,
  greenStackOwnedMats,
  hasMoney,
  hasMaterials,
  enoughPlayerStorage,
  itemReq,
  bonus
}) => {
  const storageColor = enoughPlayerStorage ? '' : '#e57373';
  const materialColor = hasMaterials ? '' : '#e57373';
  return <>
    <Typography variant={'h5'} data-sentry-element="Typography" data-sentry-source-file="stamps.jsx">{cleanUnderscore(displayName)} (Lv {level})</Typography>
    <Typography sx={{
      color: level > 0 && multiplier > 1 ? 'info.dark' : ''
    }} variant={'body1'} data-sentry-element="Typography" data-sentry-source-file="stamps.jsx">+{cleanUnderscore(effect.replace(/\+{/, notateNumber(bonus, 'MultiplierInfo').replace('.00', '')))}</Typography>
    {unobtainableStamps[displayName] ? <Typography mt={1}>(Unobtainable)</Typography> : null}
    {level > 0 ? <>
      <CostSection isMaterialCost={!(hasMoney && hasMaterials && enoughPlayerStorage)} hasMoney={hasMoney} hasMaterials={hasMaterials} enoughPlayerStorage={enoughPlayerStorage} rawName={itemReq.rawName} materialCost={materialCost} goldCost={goldCost} level={level} />
      <Divider variant={'middle'} sx={{
        bgcolor: grey[600],
        my: 1
      }} />
      {futureCosts?.map((futureCost, index) => {
        return <CostSection key={'future-' + index} showBoth={true} {...futureCost} />;
      })}
      <Stack direction={'row'} gap={2}>
        <div>
          <Typography mt={2} variant={'subtitle2'} gutterBottom>Max capacity</Typography>
          <Typography color={storageColor} variant={'body2'}>{bestCharacter?.character} ({notateNumber(bestCharacter?.maxCapacity ?? 0, 'Big')})</Typography>
        </div>
        <div>
          <Typography mt={2} variant={'subtitle2'} gutterBottom>Storage Amount</Typography>
          <Typography color={materialColor} variant={'body2'}>{notateNumber((subtractGreenStacks ? greenStackOwnedMats : ownedMats) || 0, 'Big')}</Typography>
        </div>
      </Stack>
    </> : null}
  </>;
};
const CostSection = ({
  showBoth,
  isMaterialCost,
  reduction,
  rawName,
  materialCost,
  goldCost,
  level,
  hasMoney
}) => {
  const moneyColor = showBoth ? '' : hasMoney ? '' : '#e57373';
  return <Stack direction={'row'} gap={3} alignItems={'center'} justifyContent={'space-between'} data-sentry-element="Stack" data-sentry-component="CostSection" data-sentry-source-file="stamps.jsx">
    {isMaterialCost || showBoth ? <Stack my={1} direction={'row'} alignItems={'center'} gap={1}>
      <Typography variant={'subtitle2'}>{level}</Typography>
      <ItemIcon src={`${prefix}data/${rawName}.png`} alt="" />
      <Typography variant={'subtitle2'}>{materialCost ? notateNumber(materialCost, 'Big') : null}</Typography>
    </Stack> : null}
    {!isMaterialCost || showBoth ? <CoinDisplay style={{
      color: moneyColor
    }} variant={'horizontal'} title={''} maxCoins={3} money={getCoinsArray(goldCost)} /> : null}
    {(isMaterialCost || showBoth) && reduction >= 0 ? <Typography variant={'subtitle2'}>{reduction}%</Typography> : null}
  </Stack>;
};
const StampIcon = styled.img`
  opacity: ${({
  level
}) => level === 0 ? .5 : 1};
`;
const ItemIcon = styled.img`
  width: 32px;
  height: 32px;
  opacity: ${({
  hide
}) => hide ? 0.5 : 1};
`;
export default Stamps;